import { defineComponent } from 'vue';
export default defineComponent({
  props: {
    modelValue: {
      type: Object,
      required: true
    },
    formItem: {
      type: Array,
      default: () => []
    },
    lableWidth: {
      type: String,
      default: "100px"
    },
    itemStyle: {
      type: Object,
      default: () => ({
        padding: '10px 40px'
      })
    },
    colLayout: {
      type: Object,
      default: () => ({
        xl: 6,
        lg: 8,
        md: 12,
        sm: 24,
        xs: 24
      })
    }
  },
  emits: ['update:modelValue'],
  setup(props, {
    emit
  }) {
    const handleValueChange = (value, field) => {
      emit('update:modelValue', {
        ...props.modelValue,
        [field]: value
      });
    };
    return {
      handleValueChange
    };
  }
});